.App {
  text-align: center;
}

.columns {
  width: 100%;
  height: 1%;
  display: grid;
  grid-template-columns: 1fr 30fr 1fr;
}

.button {
  font-size: 12px;
  color: white;
  background-color: #282C34;
  border-radius: 12px;
  border: 2px solid white;
}

.mediaButton {
  font-size: 35px;
  color: white;
  background-color: #282C34;
  border: 2px solid white;
}

.speedIndicator {
  font-size: 35px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 70px;
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
